import '@fortawesome/fontawesome-free/css/all.min.css';
import 'bootstrap-css-only/css/bootstrap.min.css';
import 'mdbreact/dist/css/mdb.css';
import React from "react";
import FirebaseInstance from "./FirebaseInstance";
import nextId from "react-id-generator";
const Excel = require('exceljs');

class ResultsView extends React.Component {

    constructor(props) {
        super(props);
        this.state = ({ votingResult: [], finalResults: [], resultsVisible: false, resultButtonVisible: true, isResultValid: null, voteLength: 0, codeWorkbook: null, voteWorkbook: null, isFetched: false });

        this.onClick = this.onClick.bind(this);
        this.calculateResults = this.calculateResults.bind(this);
        this.resultList = this.resultList.bind(this);
        this.resultButton = this.resultButton.bind(this);
    }

    componentDidMount() {
        this.firebase = new FirebaseInstance().firebase;
        this.initCodeFile();
        this.initVoteFile();
    }

    initVoteFile() {
        var workbook = new Excel.Workbook();
        const storageRef = this.firebase.storage().ref();
        storageRef.child('votes.xlsx').getDownloadURL().then(function (url) {
            fetch(url).then(result => {
                result.blob().then(file => {
                    file.arrayBuffer().then(buffer => {
                        workbook.xlsx.load(buffer);
                    });
                });
            });
        }).catch(function (error) {
            alert("NÃ¥got gick fel, fÃ¶rsÃ¶k igen!" + error);
        });
        this.setState({ voteWorkbook: workbook }, this.rÃ¶ven);
    }

    initCodeFile() {
        var workbook = new Excel.Workbook();
        const storageRef = this.firebase.storage().ref();
        storageRef.child('voting_codes.xlsx').getDownloadURL().then(function (url) {
            fetch(url).then(result => {
                result.blob().then(file => {
                    file.arrayBuffer().then(buffer => {
                        workbook.xlsx.load(buffer);
                    });
                });
            });
        }).catch(function (error) {
            alert("NÃ¥got gick fel, fÃ¶rsÃ¶k igen!" + error);
        });
        this.setState({ codeWorkbook: workbook }, this.rÃ¶ven);
    }

    getVotingCodes() {
        let codes = [];
        const workbook = this.state.voteWorkbook;
        workbook.getWorksheet().getColumn("B").eachCell(content => codes.push(content.text));
        return codes.slice(1);
    }

    getReferenceCodes() {
        let referenceCodes = [];
        const workbook = this.state.codeWorkbook;
        workbook.getWorksheet().getColumn("A").eachCell(content => referenceCodes.push(content.text));
        return referenceCodes;
    }

    compareVotingCodes() {
        let previousVoters = [];
        let codes = this.getVotingCodes();
        let referenceCodes = this.getReferenceCodes();
        var result = [];
        var invalidCodes = [];

        codes.forEach(code => {
            if (referenceCodes.includes(code)) {
                previousVoters.push(code);
                result.push("Giltig rÃ¶st: " + code);
                referenceCodes = referenceCodes.filter(x => x !== code);
            } else if (previousVoters.includes(code)) {
                invalidCodes.push("Ogiltig rÃ¶st, har rÃ¶stat mer Ã¤n 1 gÃ¥ng: " + code);
                this.setState({ isResultValid: false });
            } else {
                invalidCodes.push("Ogiltig rÃ¶st, ej registrerad: " + code);
                this.setState({ isResultValid: false });
            }
        });
        if (invalidCodes.length === 0) {
            this.setState({ isResultValid: true });
        }
        return [result, invalidCodes];
    }

    getElections() {
        var columnHeaders = {};
        const workbook = this.state.voteWorkbook;
        workbook.getWorksheet().getRow(1).eachCell(content => {
            if (content.text !== "TidstÃ¤mpel" && content.text !== "Valkod") {
                columnHeaders[content.text] = [];
            }
        });
        return columnHeaders;
    }

    getVotes() {
        const workbook = this.state.voteWorkbook;
        let worksheet = workbook.getWorksheet();
        let electionVotes = this.getElections();
        for (let i = 3; i <= worksheet.actualColumnCount; i++) {
            worksheet.getColumn(i).eachCell(cell => {
                for (let prop in electionVotes) {
                    if (cell.text.startsWith(prop)) {
                        let votes = electionVotes[prop];
                        votes.push(cell.text);
                        electionVotes[prop] = votes;
                    }
                }
            });
        }
        for (let prop in electionVotes) {
            electionVotes[prop].shift();
        }
        return electionVotes;
    }

    countVotes() {
        const votes = this.getVotes();
        const results = {};
        for (let prop in votes) {
            votes[prop].forEach(vote => {
                vote = vote.split(",");
                vote.forEach(vote => {
                    results[vote.trim()] = (results[vote.trim()] + 1) || 1;
                });
            });
        }
        return results;
    }

    rÃ¶ven() {
        console.log(this.state);
    }

    // --------------------------------------------------------------------------------------------------------------------------------------- //

    onClick(event) {
        this.props.history.push('/file-selector');
        event.preventDefault();
    }

    calculateResults() {
        var result = this.countVotes();
        var votingResult = [];
        this.compareVotingCodes().forEach(array => array.forEach(vote => votingResult.push(vote)));
        var objArray = Object.entries(result);
        var finalResult = [];

        objArray.forEach(([key, value]) => {
            if (value === 1) {
                var s = " rÃ¶st"
            } else {
                s = " rÃ¶ster"
            }
            finalResult.push(key + ": " + value + s);
        });
        this.setState({ votingResult: votingResult, finalResults: finalResult, resultsVisible: true, resultButtonVisible: false, voteLength: this.getReferenceCodes().length }, this.rÃ¶ven);
    }

    resultButton() {
        return (
            <div className="col text-center">
                <button type="button" className="btn btn-success m-4 btn-lg" onClick={this.calculateResults}>Visa valresultat</button>
            </div>
        );
    }

    resultList() {
        var voteLengthCorrect = this.state.voteLength === this.state.votingResult.length

        if (this.state.isResultValid && voteLengthCorrect) {
            return (
                <div className="container w-75">
                    <h1><b>Resultat</b></h1>
                    <ul className="list-group">
                        {this.state.finalResults.map((result) =>
                            <li key={nextId()} className="list-group-item"> {<h3> {result}</h3>}</li>
                        )
                        }
                    </ul>
                    <br></br>
                    <br></br>
                    <br></br>
                    <h1><b>RÃ¶stvalidering</b></h1>
                    <br></br>
                    <h3><b>Antal rÃ¶stande: {this.state.votingResult.length}</b></h3>
                    <h3><b>RÃ¶stlÃ¤ngd: {this.state.voteLength}</b></h3>
                    <br></br>
                    <ul className="list-group">
                        {this.state.votingResult.map((result) =>
                            <li key={nextId()} className="list-group-item"> {<h3> {result}</h3>}</li>
                        )
                        }
                    </ul>

                    <div className="col text-center">
                        <button type="button" className="btn btn-success m-4 btn-lg" onClick={this.onClick}>GenomfÃ¶r en ny rÃ¶stning</button>
                    </div>
                </div >
            );
        } else if (this.state.isResultValid && !voteLengthCorrect) {
            return (
                <div className="container w-75">
                    <h5><b>RÃ¶stningen Ã¤r inte giltig. RÃ¶stlÃ¤ngd och antalet rÃ¶ster stÃ¤mmer Ã¶verensstÃ¤mmer inte.</b></h5>
                    <br></br>
                    <li className="list-group-item">
                        <h5>Antal rÃ¶stande: {this.state.votingResult.length}</h5>
                        <h5>RÃ¶stlÃ¤ngd: {this.state.voteLength}</h5>
                    </li>
                    <div className="col text-center">
                        <button type="button" className="btn btn-success m-4 btn-lg" onClick={this.onClick}>GenomfÃ¶r en ny rÃ¶stning</button>
                    </div>
                </div >
            );
        }

        else if (!this.state.isResultValid && voteLengthCorrect) {
            return (
                <div className="container w-75">
                    <h5><b>RÃ¶stningen Ã¤r inte giltig. Ta bort ogiltiga rÃ¶ster ur Excel-arket och ladda upp igen: </b></h5>
                    <br></br>
                    <ul className="list-group">
                        {this.state.votingResult.map((result) =>
                            <li key={nextId()} className="list-group-item"> {<h3> {result}</h3>}</li>
                        )
                        }
                    </ul>
                    <div className="col text-center">
                        <button type="button" className="btn btn-success m-4 btn-lg" onClick={this.onClick}>GenomfÃ¶r en ny rÃ¶stning</button>
                    </div>
                </div >
            );
        } else if (!this.state.isResultValid && !voteLengthCorrect) {
            return (
                <div className="container w-75">
                    <h5><b>RÃ¶stningen Ã¤r inte giltig. Ta bort ogiltiga rÃ¶ster ur Excel-arket och ladda upp igen: </b></h5>
                    <br></br>
                    <ul className="list-group">
                        {this.state.votingResult.map((result) =>
                            <li key={nextId()} className="list-group-item"> {<h3> {result}</h3>}</li>
                        )}
                    </ul>
                    <br></br>
                    <h5><b>RÃ¶stningen Ã¤r inte giltig. RÃ¶stlÃ¤ngd och antalet rÃ¶ster stÃ¤mmer Ã¶verensstÃ¤mmer inte.</b></h5>
                    <br></br>
                    <li className="list-group-item">
                        <h5>Antal rÃ¶stande: {this.state.votingResult.length}</h5>
                        <h5>RÃ¶stlÃ¤ngd: {this.state.voteLength}</h5>
                    </li>
                    <div className="col text-center">
                        <button type="button" className="btn btn-success m-4 btn-lg" onClick={this.onClick}>GenomfÃ¶r en ny rÃ¶stning</button>
                    </div>
                </div >
            );
        }
    }

    render() {
        return (
            <div className="container w-50">
                {this.state.resultsVisible ? this.resultList() : null}
                <div className="row">

                </div>
                {this.state.resultButtonVisible ? this.resultButton() : null}
            </div>
        );
    }
}

export default ResultsView;